.wsu-gutenberg-accessibility-panel__section {
  margin-bottom: 1.5rem;
}
.wsu-gutenberg-accessibility-panel__section-heading {
  font-size: 14px !important;
  margin-bottom: 0.75em !important;
  padding-bottom: 0.55em;
  border-bottom: 1px solid #e0e0e0;
}
.wsu-gutenberg-accessibility-panel__report-table {
  border-collapse: collapse;
  width: 100%;
}
.wsu-gutenberg-accessibility-panel__report-table tr:first-child td {
  padding-top: 0;
}
.wsu-gutenberg-accessibility-panel__report-table tr:last-child td {
  padding-bottom: 0;
}
.wsu-gutenberg-accessibility-panel__report-table td {
  padding: 0.5em;
}
.wsu-gutenberg-accessibility-panel__report-table td:first-child {
  padding-left: 0;
}
.wsu-gutenberg-accessibility-panel__report-table td:last-child {
  text-align: right;
}
.wsu-gutenberg-accessibility-panel__report-empty-message {
  margin-bottom: 0;
}
.wsu-gutenberg-accessibility-panel__details-heading {
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 1.33rem;
  padding-left: 1.33rem;
  font-size: 16px;
  font-weight: 400;
}
.wsu-gutenberg-accessibility-panel__details-content {
  padding: 0 1.33rem 1.33rem 1.33rem;
}
.wsu-gutenberg-accessibility-panel__details-table {
  width: 100%;
  min-width: 350px;
  text-align: left;
  border-collapse: collapse;
}
.wsu-gutenberg-accessibility-panel__details-table td,
.wsu-gutenberg-accessibility-panel__details-table th {
  padding: 0.5em 1em;
}
.wsu-gutenberg-accessibility-panel__details-table td:first-child,
.wsu-gutenberg-accessibility-panel__details-table th:first-child {
  padding-left: 0;
}
.wsu-gutenberg-accessibility-panel__details-table td:last-child,
.wsu-gutenberg-accessibility-panel__details-table th:last-child {
  padding-right: 0;
}
.wsu-gutenberg-accessibility-panel__details-table th {
  font-weight: 500;
  white-space: nowrap;
}
.wsu-gutenberg-accessibility-panel__details-table td {
  border-top: 1px solid #e0e0e0;
}
.wsu-gutenberg-accessibility-panel__details-table img {
  width: 100px;
}
.wsu-gutenberg-accessibility-panel__wave-link {
  padding: 6px 12px;
  display: flex;
  align-items: end;
  justify-content: space-between;
  border: 1px solid;
  background: #007cba;
  color: white;
  text-decoration: none;
}
.wsu-gutenberg-accessibility-panel__wave-link:hover, .wsu-gutenberg-accessibility-panel__wave-link:focus, .wsu-gutenberg-accessibility-panel__wave-link:active {
  color: white;
  background-color: #006699;
}
.wsu-gutenberg-accessibility-panel .dashicons-yes-alt, .wsu-gutenberg-accessibility-panel__details-table .dashicons-yes-alt {
  color: #6c923d;
}
.wsu-gutenberg-accessibility-panel .dashicons-dismiss, .wsu-gutenberg-accessibility-panel__details-table .dashicons-dismiss {
  color: #d42127;
}
.wsu-gutenberg-accessibility-panel .dashicons-warning, .wsu-gutenberg-accessibility-panel__details-table .dashicons-warning {
  color: #f67428;
}
.wsu-gutenberg-accessibility-panel .dashicons-flag, .wsu-gutenberg-accessibility-panel__details-table .dashicons-flag {
  color: #f3b81c;
}
.wsu-gutenberg-accessibility-panel .dashicons-minus, .wsu-gutenberg-accessibility-panel__details-table .dashicons-minus {
  color: #c3c4c7;
}

.wsu-plugin-gutenberg-admin-dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin: 2px;
  display: inline-block;
}
.wsu-plugin-gutenberg-admin-dot--red {
  background-color: #d42127;
}
.wsu-plugin-gutenberg-admin-dot--orange {
  background-color: #f67428;
}
.wsu-plugin-gutenberg-admin-dot--yellow {
  background-color: #f3b81c;
}